html, body {
  height: 100%;
  color: #fff;
  background-color: #000;
  flex-direction: column;
  margin: 0;
  font-family: Avenir Next, Avenir, Helvetica, sans-serif;
  font-size: 14px;
  display: flex;
}

header {
  justify-content: flex-start;
  padding: 20px 20px 0;
  display: flex;
}

#logo {
  height: 70px;
}

main {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

#instructions {
  text-align: center;
  margin-bottom: 10px;
  font-weight: 500;
}

#drop-zone {
  width: 50vw;
  min-width: 300px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  height: 40vh;
  background-color: #010101;
  border: 2px dashed #ccc;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  padding: 20px;
  font-weight: 700;
  display: flex;
}

::file-selector-button {
  display: none;
}

#drop-zone.is-dragover {
  background-color: #4d4d4d;
  border-color: #a5c9ff;
}

a {
  color: #a5c9ff;
  margin-top: 10px;
  text-decoration: underline;
}

a:hover {
  color: #ff7c89;
}

